.heading {
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;
  grid-template-rows: 50px;
  background-color: #0f274a;
  margin: 20px 0px 0px 20px; }

.one {
  grid-column: 1;
  grid-row: 1;
  color: white;
  font-family: Nova Flat;
  font-size: 18px;
  font-weight: bold;
  padding: 10px 10px 10px 15px;
  width: 120px; }

.icon {
  padding: 5px 5px 0px 5px; }

.two {
  grid-column: 2;
  grid-row: 1; }

.three {
  grid-column: 3;
  grid-row: 1; }

.itemWrapper {
  border: 1px solid #e0dfe0;
  box-sizing: border-box;
  margin: 0px 0px 10px 20px;
  height: 230px;
  overflow-y: scroll;
  overflow-x: hidden; }

.itemNameAndOwnerWrapper {
  display: inline-block;
  /*width: 100%;*/ }

.item {
  text-align: left;
  padding-left: 40%;
  color: #0f274a;
  font-family: Nova Flat;
  font-size: 15px;
  font-weight: bold;
  border: 1px solid #e0dfe0;
  margin: 5px 5px 5px 5px;
  height: 50px;
  -webkit-box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75); }

.item:hover {
  background-color: #0f274a;
  color: white; }

.itemOwner {
  text-align: right;
  font-size: 8px;
  display: inline-block;
  float: right; }

.dot {
  height: 10px;
  width: 10px;
  background-color: green;
  border-radius: 50%;
  display: inline-block;
  margin: 2px 2px 2px 2px; }

.itemName {
  text-align: center;
  display: inline-block;
  padding-top: 15px; }

.removeItemWrapper {
  display: inline-block;
  float: right;
  background-color: #e74343;
  color: white;
  height: 100%;
  width: 100px; }

.textbox {
  border-radius: 25px;
  border: 2px solid lightgrey;
  width: 300px;
  height: 30px;
  background-size: 20px;
  background-repeat: no-repeat;
  background-position: left;
  background-color: #f6f6f6;
  margin-top: 10px;
  padding-left: 10px;
  -webkit-box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75); }

.textarea {
  border-radius: 25px;
  border: 2px solid lightgrey;
  width: 300px;
  height: 100px;
  background-size: 20px;
  background-repeat: no-repeat;
  background-position: left;
  background-color: #f6f6f6;
  margin-top: 20px;
  padding-left: 10px;
  -webkit-box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75); }
