.container {
  border: 1px solid #e0dfe0;
  margin: 20px 0px 0px 0px;
  height: 278px; }

.resultsContainer {
  height: 230px;
  overflow-y: scroll;
  overflow-x: hidden; }

.resultsItem {
  text-align: center;
  color: #0f274a;
  font-family: Nova Flat;
  font-size: 15px;
  font-weight: lighter;
  height: 20px;
  margin-top: 10px;
  margin-left: 15%;
  margin-right: 15%;
  -webkit-box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.75); }

.resultsItem:hover {
  background-color: #0f274a;
  color: white; }

.textbox {
  border-radius: 25px;
  border: 2px solid lightgrey;
  padding: 20px;
  width: 50%;
  height: 1px;
  background-image: url("./img/mic.png");
  background-size: 20px;
  background-repeat: no-repeat;
  background-position: left;
  background-color: #f6f6f6;
  margin-left: 20%;
  margin-top: 10px;
  -webkit-box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 4px 2px 0px rgba(0, 0, 0, 0.75); }

.box {
  display: flex;
  flex-wrap: wrap;
  height: 200px;
  overflow-y: scroll; }

.box > * {
  flex: 1 1 160px;
  margin: 10px 10px 10px 10px;
  box-shadow: 1px 2px 0px 0px rgba(0, 0, 0, 0.75);
  background-image: linear-gradient(to bottom, white, gainsboro);
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  border: 1px solid #e0dfe0;
  font-family: Nova Flat;
  font-size: 20px;
  font-weight: lighter; }
