.heading {
  -webkit-box-shadow: 0px 6px 0px 0px #73ba60;
  -moz-box-shadow: 0px 6px 0px 0px #73ba60;
  box-shadow: 0px 6px 0px 0px #73ba60; }

.message {
  display: inline-block;
  margin-left: 10%;
  margin-right: 10%;
  text-align: center;
  font-family: 'Raleway', sans-serif;
  font-size: 14px;
  font-weight: 100;
  line-height: 36px; }

.buttonContainer {
  display: inline-block;
  margin: 5px; }

.button {
  background-color: white;
  /*margin: 10px;*/
  border: 1px solid darkgrey;
  color: black;
  padding: 5px 10px;
  height: 35px;
  width: 100px;
  text-align: center;
  text-decoration: none;
  font-size: 16px; }

.button:hover {
  background-color: #0f274a;
  color: white; }

.example-appear {
  opacity: 0.01; }

.example-appear.example-appear-active {
  opacity: 1;
  transition: opacity 0.5s ease-in; }
